// portfolio

// список робіт
.portfolio {
  @media screen and (min-width: $tablet) {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 450px;

    @media screen and (min-width: $tablet) {
      max-width: 354px;
    }

    @media screen and (min-width: $desktop) {
      max-width: 370px;
    }
  }

  // обкладинка
  &__cover {
    position: relative;
    overflow: hidden;
  }

  // опис роботи
  &__job-description {
    color: $color-white;
    padding: 63px 24px;
    font-size: 18px;
    line-height: 1.555;
    letter-spacing: 0.03em;
  }

  &__text-content {
    flex-grow: 1;
    padding: 20px 24px;
    border: 1px solid $color-border-box;
    border-top: 0;
  }

  &__subtitle {
    color: $color-secondary;
    font-weight: $font-weight-more;
    font-size: 18px;
    line-height: 2;
    letter-spacing: 0.06em;
    white-space: wrap;
  }

  &__desc {
    margin-top: 4px;
    color: $color-primary;
    font-size: $font-size-more;
    line-height: 1.875;
    letter-spacing: 0.03em;
  }
}

.portfolio__box {
  transition: box-shadow 250ms $timing-function;

  @media screen and (max-width: 767px) {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  @media screen and (min-width: $tablet) {
    @include flex-basis(2, 30px);
  }

  @media screen and (min-width: $desktop) {
    @include flex-basis(3, 30px);
  }

  &:hover,
  &:focus {
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }
}

// обкладинка
.portfolio__overlay {
  position: absolute;
  top: 0;
  left: 0;
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: $color-background-box;

  transform: translateY(100%);
  transition: transform 250ms $timing-function;

  .portfolio__box:hover & {
    transform: translateY(0);
  }

  &:hover,
  &:focus {
    transform: translateY(0);
  }
}
