// contact
.contact {
  display: none;
  margin-left: auto;

  @media screen and (min-width: $tablet) {
    @include centeringFlex($level: flex-start);
    flex-direction: column;
    gap: 10px;
  }
  @media screen and (min-width: $desktop) {
    @include centeringFlex($level: flex-start);
    flex-direction: row;
    gap: 50px;
  }

  // іконки мейл. тел.
  &__icon {
    align-self: center;
  }
}

// розмір іконок контактів для планшету
@media screen and (max-width: 1199px) {
  .contact__icon--envelope-small {
    width: 14px;
    height: 10px;
  }

  .contact__icon--phone-small {
    width: 10px;
    height: 14px;
  }
}

// Мейл. тел.
.contact__link {
  display: flex;
  color: $color-primary;
  font-weight: $font-weight-less;
  font-size: 34px;
  line-height: 1.176;
  fill: currentColor;

  transition: color 250ms $timing-function;

  @media screen and (min-width: $tablet) {
    padding: 0;
    gap: 8px;
    font-size: 12px;
    line-height: 1.167;
  }
  @media screen and (min-width: $desktop) {
    padding: 32px 0;
    gap: 10px;
    font-size: $font-size-less;
    line-height: 1.143;
  }

  &:hover,
  &:focus {
    color: $color-accent;
  }
}
